.banner {
  display: flex;
  position: relative;
  height: 430px;
}

.banner__text {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, black, transparent);
  color: #ffffff;
}

.banner__text div {
  width: 420px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20%;
  color: #ffffff;
  text-shadow: 1px 1px 4px #000000;
  font-size: 22px;
}

.banner__text h1 {
  font-weight: 800;
  line-height: 35pt;
}

.banner__image {
  width: 100%;
  object-fit: cover;
  margin: 0 0 0 auto;
}

@media (max-width: 400px) {
  .banner__text div {
    margin-left: 20px;
  }

  .banner__image {
    width: 100%;
  }

  .banner__text p {
    width: 90vw;
    font-size: 5vmin;
    line-height: 25pt;
  }

  .banner__text h1 {
    width: 90vw;
    font-size: 10vmin;
    line-height: 25pt;
  }
}
